.button{
    width: 100%;
    border: 1px solid #000000;
    padding: 0px;
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
    font-size: 20px;
    line-height: 30px;
    text-align: left;
    position: relative !important;
    margin-bottom: 10px;
    cursor: pointer;
    text-transform: uppercase;
    display: block !important;
    text-decoration: none !important;
    position: relative;
    overflow: hidden;
    z-index: 1;
    -webkit-transition: all 0.2s ease-in;
    -o-transition: all 0.2s ease-in;
    transition: all 0.2s ease-in;
  }
  .button-transition{
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
    z-index: 1;
    padding: 15px !important;
  }
  .button-transition:after {
    content: "";
    position: absolute;
    top: 0px;
    left: 0px;
    width: 500%;
    height: 1000%;
    background: #34495E;
    z-index: -1;
    -webkit-transform-origin: 0% 0%;
        -ms-transform-origin: 0% 0%;
            transform-origin: 0% 0%;
    -webkit-transform: translateX(calc(20% - 25px)) translateY(10%) rotate(-45deg);
        -ms-transform: translateX(calc(20% - 25px)) translateY(10%) rotate(-45deg);
            transform: translateX(calc(20% - 25px)) translateY(10%) rotate(-45deg);
    -webkit-transform: translateY(10%) translateX(20%) rotate(-45deg);
        -ms-transform: translateY(10%) translateX(20%) rotate(-45deg);
            transform: translateY(10%) translateX(20%) rotate(-45deg);
    -webkit-transition: -webkit-transform .2s;
    transition: -webkit-transform .2s;
    -o-transition: transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;

  }
  .button-transition:hover::after {
    -webkit-transform: translateY(10%) translateX(-7%) rotate(-45deg);
        -ms-transform: translateY(10%) translateX(-7%) rotate(-45deg);
            transform: translateY(10%) translateX(-7%) rotate(-45deg);
  }
  @media screen and (min-width:991px) and (max-width:1200px){
    .button-transition:hover::after {
      -webkit-transform: translateY(5%) translateX(-7%) rotate(-45deg);
          -ms-transform: translateY(5%) translateX(-7%) rotate(-45deg);
              transform: translateY(5%) translateX(-7%) rotate(-45deg);
    }
  }
  .button:hover{
    -webkit-transition: all initial .3s;
    -o-transition: all initial .3s;
    transition: all initial .3s;
    pointer-events: auto;
  }


  .buttonText{
    z-index: 10;
    display:block; 
    position:relative;
    text-decoration: none !important;
  }


